body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  background-image: url(/static/media/header-image.5aa98db0.jpg);
  background-position: center;
  background-size:cover;
  height: 89vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
}


.navbar {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 2rem;
  background-color: rgba(3, 38, 95, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  transition: background-color 0.2s ease;
  height: 11vh;
  transition: 0.5s ease-in-out;
}

.navbar:hover {
  background-color: rgba(0, 0, 0, 1);
}

.navbar-scrolled {
  height: 7vh; /* or whatever height you want */
  transition: 0.3s ease;
}


.logo {
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  text-transform: uppercase; 
  letter-spacing: 2px; 
}

.menu {
  display: flex;
  list-style-type: none;
  padding: 0;
}

.menu li {
  margin-left: 1rem;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease; 
}

.menu li:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.menu a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
  transition: color 0.3s ease; 
}

.menu a:hover {
  color: #ffd700;
  font:bold;

}





.header-content {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 10px;
  opacity: 0; /* Rend le contenu transparent */
  transition: opacity 2s ease-in-out; /* Spécifie la transition à appliquer à la propriété opacity */
}

.header-content.visible {
  opacity: 1; /* Rend le contenu opaque */
}
.header-content h2 {
  color: white;
  margin: 0;
}
.header-content p {
  color: white; /* Couleur du texte */
  font-size: 1.2em; /* Taille du texte */
  line-height: 1.6em; /* Espacement des lignes */
  text-align:center; /* Alignement du texte */
  padding: 15px; /* Espacement interne */
  margin: 15px 0; /* Espacement externe */
  background-color: rgba(3, 38, 95, 0.6); /* Arrière-plan semi-transparent */
  border-radius: 5px; /* Coins arrondis */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Ombre de texte */
  width: 40vw;
}


.hoverable-letter {
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.hoverable-letter:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5); /* ajustez le facteur d'échelle au besoin */
  color: red;
}

.escape-div{
  transition: "transform 0.5s ease-out"
}

.header-small{
  height: 53vh;
}

.noStyle {
  color: inherit;
  text-decoration: none;
}
footer {
    background-color: #1F2833;
    color: #C5C6C7;
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-wrap: wrap;
  }
  
  .footer-left,
  .footer-center,
  .footer-right {
    flex: 1 1;
    margin: 0 15px;
  }
  
  .footer-left ul,
  .footer-right ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-left h4,
  .footer-center h3,
  .footer-right h4 {
    color: #66FCF1;
    margin-bottom: 10px;
  }
  
  .footer-left ul li,
  .footer-right ul li {
    margin: 5px 0;
  }
  
  .footer-right ul.socials li {
    display: inline-block;
    margin-right: 10px;
  }
  
  .footer-left ul li a,
  .footer-right ul li a {
    color: #C5C6C7;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .footer-left ul li a:hover,
  .footer-right ul li a:hover {
    color: #66FCF1;
  }
  
  .footer-bottom {
    width: 100%;
    text-align: center;
    padding: 10px 0;
    color: #C5C6C7;
    margin-top: 20px;
    border-top: 1px solid #C5C6C7;
  }
  
main {
    padding: 20px;
  }


  .main-content {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    text-align: justify;
    font-size: 1.1rem;
    color: #333;
    line-height: 1.6;
  }
  
  .main-content h2 {
    font-size: 2.5rem;
    color: #007BFF;
    margin-bottom: 1rem;
  }
  
  .main-content p {
    margin-bottom: 1rem;
  }
  
.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
  }
  
  .contact h1 {
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .contact form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
  }
  
  .contact form label {
    font-weight: bold;
    margin: 0.5rem 0;
  }
  
  .contact form input, .contact form textarea {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .contact form input[type="submit"] {
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 4px;
    margin-top: 1rem;
    padding: 0.5rem;
    transition: background 0.3s ease;
  }
  
  .contact form input[type="submit"]:hover {
    background-color: #45a049;
  }
  .btnCopied {
    background-color:rgb(253, 213, 161);
    border-radius: 19px;
    border: transparent;
    color: #007bff; /* La couleur du texte est bleue */
    text-decoration: none; /* Le texte est souligné */
    cursor: pointer; /* Le curseur change lorsque vous survolez le bouton */
    padding: 7px; /* Pas de rembourrage */
    margin-left: 11px;
    font-family: inherit; /* Utilise la même police que le reste de la page */
    font-size: inherit; /* Utilise la même taille de police que le reste de la page */
    transition: color 0.9s; /* Animation fluide lorsque la couleur change */
  }
  
  .btnCopied:hover {
    color: #014389; /* La couleur du texte change lorsque vous survolez le bouton */
    background-color:antiquewhite;
    padding: 7px; /* Pas de rembourrage */
    margin-left: 11px;
    font-family: inherit; /* Utilise la même police que le reste de la page */
    font-size: inherit; /* Utilise la même taille de police que le reste de la page */
  }
  
.about-page {
    width: 80%;
    margin: auto;
    padding: 20px;
  }
  
  .about-page h1, .about-page h2 {
    color: #1F2833;
    padding: 10px 0;
  }
  
  .about-page p {
    line-height: 1.6;
    color: #4B4B4B;
    padding: 10px 0;
  }
  
.services {
    padding: 2rem;
    background-color: #f7f7f7;
    font-family: Arial, sans-serif;
    text-align: center;
}

.services h1 {
    color: #333;
    text-transform: uppercase;
    margin-bottom: 2rem;
}

.services p {
    color: #666;
    margin-bottom: 2rem;
    line-height: 1.6;
}

.services__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: stretch;
}

.service-card {
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 2rem;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(50% - 4rem);
    margin: 2rem 1rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.3s ease;
}

.service-card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.service-card h2 {
    color: #333;
    margin-bottom: 1rem;
}

.service-card p {
    color: #666;
}

